___CSS_LOADER_EXPORT___.push([module.id, `body {
  display: grid;
  justify-items: center;
  grid-template-rows: 8fr 1fr;
  grid-template-columns: 1fr 1fr;
}
#imgContainer {
  grid-column: 1/3;
  display: flex;
  justify-content: center;
  width: 500px;
}
#leftButton,
#rightButton {
  grid-row: 2/3;
  width: 50px;
}

#currentImage {
  grid-row: 1/2;
  grid-column: 1/3;
  width: 500px;
  height: 500px;
}

.slideLeft {
  animation: Slide-left 800ms ease-in-out reverse forwards;
}

@keyframes Slide-left {
  0% {
    margin-left: 100%;
    opacity: 0;
    width: 0px;
  }

  100% {
    margin-left: 0px;
    opacity: 1;
    width: 100%;
  }
}

.newImage {
  animation: newImage 800ms ease-in-out normal forwards;
}
@keyframes newImage {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.slideRight {
  animation: Slide-right 800ms ease-in-out backwards reverse;
}
@keyframes Slide-right {
  0% {
    margin-right: 100%;
    opacity: 0;
    width: 0%;
  }
  100% {
    margin-right: 0px;
    opacity: 1;
    width: 100%;
  }
}
